.header {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 3;
    padding-top: 30px;

    .menu {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .logo {
            margin-right: 100px;
        }

        &__list {
            display: flex;
            margin-right: auto;
        }

        &__link {
            font-size: 20px;
            line-height: 24px;
            color: #585858;
        }

        &__item:not(:last-child) {
            margin-right: 20px;
        }

        &__button {
            padding: 5px;
            width: 100%;
            max-width: 195px;
            border: none;
            border-radius: 10px;
            font-family: "Pacifico", sans-serif;
            font-size: 16px;
            line-height: 22px;
            color: $wh;
            background-color: $accent;
            cursor: pointer;
        }
    }
}